@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

hr {
  margin: 0;
  border: none;
  height: 0.1rem;
  width: 100%;
  background: lightslategray;
}

.pokecard {
  border: 2px solid lightslategray;
  width: 6rem;
  background: lightblue;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  -webkit-tap-highlight-color: transparent;
}

.pokecard > h3 {
  font-family: 'Roboto', sans-serif;
  font-size: 0.9rem;
  color: black;
}

@media only screen and (min-width: 700px) {
  .pokecard {
    width: 10rem;
    transition: all 0.2s ease-in-out;
  }

  .pokecard:hover {
    transform: scale(1.1);
  }

  .pokecard > h3 {
    font-size: 1.2rem;
  }
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  padding: 1rem 0;
}

@media only screen and (min-width: 700px) {
  .container {
    grid-gap: 1.5rem;
    gap: 1.5rem;
    padding: 1rem;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(255, 0, 0, 0.781);
}

.navbar > h1 {
  font-size: 1.5rem;
  font-family: 'Pokemon', sans-serif;
  letter-spacing: 0.1rem;
  color: white;
  padding: 0.5rem 0;
  -webkit-text-stroke: 1px black;
}

.navbar-scores {
  display: flex;
  justify-content: space-around;
  padding-bottom: 0.5rem;
}

.navbar-scores > p {
  font-family: 'Pokemon', sans-serif;
  font-size: 1rem;
  padding: 0 0.5rem;
  color: white;
  letter-spacing: 0.1rem;
  -webkit-text-stroke: 0.5px black;
}

@media only screen and (min-width: 700px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    padding: 0.5rem 1rem;
  }
  .navbar > h1 {
    font-size: 2.5rem;
    letter-spacing: 0.3rem;
    padding: 0;
  }

  .navbar-scores {
    padding: 0;
  }

  .navbar-scores > p {
    font-size: 1.2rem;
    padding: 0 1rem;
  }
}

.gameover {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgb(37, 37, 37);
}

.gameover > h1 {
  color: rgba(255, 0, 0, 0.781);
  font-family: 'Pokemon', sans-serif;
  font-size: 3rem;
  letter-spacing: 0.3rem;
  -webkit-text-stroke: 1px black;
}

.gameover > h2 {
  font-family: 'Pokemon', sans-serif;
  font-size: 1.5rem;
  color: white;
  -webkit-text-stroke: 1px black;
  letter-spacing: 0.1rem;
}

.gameover > button {
  font-family: 'Roboto', sans-serif;
  margin-top: 0.5rem;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 0.2s ease-in-out;
}

.gameover > button:hover {
  background: rgba(245, 245, 245, 0.753);
  transform: scale(1.1);
}

.gameover > button:focus {
  outline: none;
  border: 1px solid black;
}

@media only screen and (min-width: 700px) {
  .gameover > h1 {
    font-size: 6rem;
    letter-spacing: 0.6rem;
  }

  .gameover > h2 {
    font-size: 3rem;
    letter-spacing: 0.2rem;
  }

  .gameover > button {
    font-size: 1.5rem;
    padding: 1rem 1.5rem;
  }
}

.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.507);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-container {
  background: white;
  font-family: 'Roboto', sans-serif;
  padding: 1rem 1.5rem;
  border-radius: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.modal-title {
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: 0.5rem;
}

.modal-text {
  font-weight: 400;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

.modal-btn {
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 0.2s ease-in-out;
  background: rgb(65, 181, 253);
  color: white;
  margin: auto;
}

.modal-btn:hover {
  background: rgba(111, 197, 250, 0.911);
}

.modal-btn:focus {
  outline: none;
  border: 1px solid black;
}

@media only screen and (min-width: 700px) {
  .modal-container {
    padding: 1.5rem 2rem;
  }

  .modal-title {
    font-size: 2rem;
  }

  .modal-text {
    font-size: 1.2rem;
  }

  .modal-btn {
    font-size: 1.2rem;
  }
}

@font-face {
  font-family: 'Pokemon';
  src: url(/memory-card/static/media/Pokemon.5d60be1f.ttf);
}

#root {
  height: 100vh;
}

button {
  background: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 10rem;
}

.memory-card-game {
  background: rgb(37, 37, 37);
  height: 100%;
}

